import * as styleConstants from  '../constants/StyleConstants';
import 'react';
/*
 * For 2 columns: Mark first col first={true} do not mark last
 * For 3+ columns: Mark first col first={true} and last col last={true} 
 */
export default class Col extends React.Component{
    render(){
        return <div className="col">
        <style jsx>{`
            .col {
                flex:1;
                ${this.props.first ? `padding-right: ${styleConstants.marginNum / 2}px` : `padding-left: ${styleConstants.marginNum / 2}px`}
            }
        `}</style>
            {this.props.children}
    </div>
    }
}